<h2 id="introduction" tabindex="-1"><a class="header-anchor" href="#introduction" aria-hidden="true">#</a> Introduction</h2>
<p>In this course you will learn what the web is and how you can create web applications running on it. <FigureNumber /> below displays the architecture of a typical web application.</p>
<Figure caption="Common web application architecture.">
<RenderMermaid graph-definition="
graph LR
	users[Users]
	subgraph Users' computers
		webBrowser[Web Browser]
	end
	subgraph Web Server
		webApp[Web Application]
	end
	subgraph Database Server
		db[Database]
	end
	users -- Use --> webBrowser
	webBrowser -- HTTP --> webApp
	webApp -- SQL --> db
" />
</Figure>
<p>More specifically you will learn how to create a web application to which a single user can login to modify the resources on it, such as blog posts and guestbook posts. To accomplish this your web application will use an SQLite database to store the resources and the web framework Express to handle incoming HTTP requests. To avoid spending too much time on the graphical design of the web application you will also learn how to use a CSS framework with pre-styled graphical user interface components you can use. Your web application must of course also be secure, so you will also learn about common vulnerabilities in web applications and how you can prevent them.</p>
