<h2 id="learning-activities" tabindex="-1"><a class="header-anchor" href="#learning-activities" aria-hidden="true">#</a> Learning Activities</h2>
<p>Here are short introduction to the learning activities offered in the course.</p>
<h3 id="recorded-lectures" tabindex="-1"><a class="header-anchor" href="#recorded-lectures" aria-hidden="true">#</a> Recorded Lectures</h3>
<p>In the beginning of most weeks you will be encouraged to view some recorded lectures. These will be quite theoretical and will teach new you things you can use when building a website.</p>
<h3 id="tutorials" tabindex="-1"><a class="header-anchor" href="#tutorials" aria-hidden="true">#</a> Tutorials</h3>
<p>Some weeks there will be a tutorial session where the teacher will show you how to use what has been taught in the recorded lectures in practice.</p>
<h3 id="lab-sessions" tabindex="-1"><a class="header-anchor" href="#lab-sessions" aria-hidden="true">#</a> Lab Sessions</h3>
<p>Each week you may attend a 4 hours lab session. At the lab session you may ask the teacher there for help with your exercises and project work.</p>
<p>There are 3 different lab sessions each week, but you may only sign up for one of them. To distribute you, join one of <a href="https://ju.instructure.com/courses/4558/groups" target="_blank" rel="noopener noreferrer">the lab groups on Canvas<OutboundLink/></a>. First come, first served.</p>
<div class="custom-container tip"><p class="custom-container-title">Attending more lab sessions</p>
<p>You may attend the other lab sessions if there is room for it, but the teacher there must of course prioritize helping the students that have signed up for that lab session.</p>
</div>
<h2 id="examination-tests" tabindex="-1"><a class="header-anchor" href="#examination-tests" aria-hidden="true">#</a> Examination Tests</h2>
<p>The course contains two examination tests.</p>
<h3 id="_1-written-examination" tabindex="-1"><a class="header-anchor" href="#_1-written-examination" aria-hidden="true">#</a> 1. Written Examination</h3>
<p>2 credits, graded U, 3, 4, and 5. More information is available on the <RouterLink to="/courses/web-development-fundamentals/written-examination/">Written Examination</RouterLink> page.</p>
<h3 id="_2-project-work" tabindex="-1"><a class="header-anchor" href="#_2-project-work" aria-hidden="true">#</a> 2. Project Work</h3>
<p>5.5 credits, graded U, 3, 4 and 5. More information is available on the <RouterLink to="/courses/web-development-fundamentals/project-work/">Project Work</RouterLink> page.</p>
<h3 id="final-grade" tabindex="-1"><a class="header-anchor" href="#final-grade" aria-hidden="true">#</a> Final Grade</h3>
<p>To receive a final grade on the course you need to pass both examination tests (<code>Written Examination</code> and <code>Project Work</code>). The final grade will be based on the lowest grade of the <code>Written Examination</code> and the <code>Project Work</code>, but these grades will also be marked as <code>weak</code> or <code>strong</code>. If one of the grades is lower than the other and has been marked as <code>strong</code>, the final grade will be the lower grade + 1. Examples:</p>
<ul>
<li>A <code>strong 3</code> and a <code>weak 4</code> gives you <code>4</code> as the final grade.</li>
<li>A <code>weak 3</code> and a <code>strong 5</code> gives you <code>3</code> as the final grade.</li>
</ul>
<p>For the <code>Written Examination</code>, 50% of max score is a strong 3, and 70% of max score is a strong 4.</p>
<h3 id="re-examination" tabindex="-1"><a class="header-anchor" href="#re-examination" aria-hidden="true">#</a> Re-examination</h3>
<FigureNumber /> below shows the study and exam periods during a year. The small blue circle sectors shows when the (re-)examinations are offered in this course.
<AcademicYearFigure />
<p>Re-examination for the <code>Written Examination</code> is offered at the re-exam periods in January and August.</p>
<p>Re-examination for the <code>Project Work</code> is offered at the re-exam periods in January and August. If you need to know a precise date and time in advance, email the course coordinator at <code>peter.larsson-green@ju.se</code>. The project work will only be graded U/3 at re-examination.</p>
<p>If you haven't passed some examination tests after the three examination opportunities (ordinary + 2 re-exam) you need to re-take those tests the next time the course run. Note that the instructions for those tests might have changed the next time the course run, and that you should take them according to the new instructions.</p>
